// npm i polygon-clipping
import * as pc from "polygon-clipping";

/** Utilities **/
const closeRing = (ring) =>
  (ring.length && ring[0][0] === ring[ring.length - 1][0] && ring[0][1] === ring[ring.length - 1][1])
    ? ring
    : [...ring, ring[0]];

const declose = (ring) =>
  (ring.length && ring[0][0] === ring[ring.length - 1][0] && ring[0][1] === ring[ring.length - 1][1])
    ? ring.slice(0, -1)
    : ring;

const add = ([x,y], [vx,vy]) => [x+vx, y+vy];

function centroidOfRing(ring) { // shoelace centroid, ring *not* closed
  let A = 0, Cx = 0, Cy = 0;
  for (let i = 0; i < ring.length; i++) {
    const [x1,y1] = ring[i];
    const [x2,y2] = ring[(i+1) % ring.length];
    const cross = x1*y2 - x2*y1;
    A  += cross;
    Cx += (x1 + x2) * cross;
    Cy += (y1 + y2) * cross;
  }
  A *= 0.5;
  return [Cx/(6*A), Cy/(6*A)];
}


export function sweepUnion(buildingRing, shadowRing) {
  const ringA = declose(buildingRing);
  const ringB = declose(shadowRing);

  // Calculate the translation vector from first vertex of each ring
  // This represents the actual shadow translation vector
  const v = [ringB[0][0] - ringA[0][0], ringB[0][1] - ringA[0][1]];

  // Build polygons to union: building polygon, translated polygon, and connecting parallelograms
  const pieces = [];

  pieces.push([ closeRing(ringA) ]);                    // Original building
  pieces.push([ closeRing(ringB) ]);                    // Shadow polygon

  // Create parallelograms connecting each edge of the building to its shadow
  for (let i = 0; i < ringA.length; i++) {
    const p1 = ringA[i];
    const p2 = ringA[(i+1) % ringA.length];
    if (p1[0] === p2[0] && p1[1] === p2[1]) continue; // skip zero-length edge
    
    // Shadow endpoints for this edge
    const q1 = ringB[i];
    const q2 = ringB[(i+1) % ringB.length];
    
    // Create quadrilateral connecting building edge to shadow edge
    const quad = closeRing([p1, p2, q2, q1]);
    pieces.push([ quad ]);
  }

  // Union all pieces; pc.union accepts (Polygon|MultiPolygon) ...args
  const mp = pc.union(...pieces); // MultiPolygon array-of-polygons-of-rings

  // Return just the coordinate array for the first polygon ring
  if (mp.length > 0 && mp[0].length > 0) {
    return mp[0][0]; // First polygon, first ring
  }
  
  // Fallback to simple concatenation if union fails
  return closeRing([...ringA, ...ringB.reverse()]);
}